import { useState } from 'react';
import { Helmet } from 'react-helmet';
import companies from '../../../data/companies';
import { arrayPaginate } from '../../../helpers';
import { titlePageGenerator } from '../../../utils';
import Button from '../../atoms/Button';
import Gap from '../../atoms/Gap/Gap';
import Heading2 from '../../atoms/Heading2/Heading2';
import TawkToWrapper from '../../atoms/TawkToWrapper/TawkToWrapper';
import AnimatedPage from '../../containers/AnimatedPage/AnimatedPage';
import SectionWrapper from '../../containers/SectionWrapper/SectionWrapper';
import Footer from '../../molecules/Footer/Footer';
import Header from '../../molecules/Header/Header';
import VacancyCard from '../../molecules/VacancyCard/VacancyCard';

export default function VacancyPage() {
  const [page, setPage] = useState(1);

  const vacancies = companies.filter(
    (company) => company.vacancies && company.id !== 'blankCompany'
  );

  const paginateData = arrayPaginate(vacancies, 10, page);
  const maxPage = Math.ceil(vacancies.length / 10);

  const handlePrev = () => {
    if (page === 1) return;
    window.scroll({
      behavior: 'smooth',
      top: 0,
    });
    setPage(page - 1);
  };

  const handleNext = () => {
    if (page === maxPage) return;
    window.scroll({
      behavior: 'smooth',
      top: 0,
    });
    setPage(page + 1);
  };

  const handleChangePage = (choosenPage: number) => {
    setPage(choosenPage);
    window.scroll({
      behavior: 'smooth',
      top: 0,
    });
  };

  return (
    <>
      <Helmet>
        <title>{titlePageGenerator('Vacancy List')}</title>
      </Helmet>
      <Header active="/vacancy" />
      <TawkToWrapper />

      <main className="min-h-screen">
        <AnimatedPage>
          <Heading2 title="Coming Soon Vacancy List" />
        </AnimatedPage>
      </main>
      <Footer isEmployer={false} />
    </>
  );
}
